"use client"

import { useEffect, useState } from "react"
import { useParams } from "next/navigation"
import VideoConference from "@/components/VideoConference"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import Link from "next/link"

interface Course {
  id: string
  name: string
  description: string
  teacher: string
  schedule: string
  materials: string[]
}

export default function CourseDetail() {
  const params = useParams()
  const courseId = params.id as string

  const [course, setCourse] = useState<Course | null>(null)

  useEffect(() => {
    // Mock data - In a real application, this would be fetched from an API
    const mockCourse: Course = {
      id: courseId,
      name: "Matemáticas Avanzadas",
      description: "Curso de cálculo diferencial e integral para estudiantes avanzados",
      teacher: "Dr. Juan Pérez",
      schedule: "Lunes y Miércoles 10:00 - 12:00",
      materials: [
        "Libro de Cálculo - Stewart",
        "Notas del profesor",
        "Ejercicios prácticos",
      ],
    }
    setCourse(mockCourse)
  }, [courseId])

  if (!course) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <p>Cargando...</p>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="container mx-auto px-4 py-8">
        {/* Navigation */}
        <div className="mb-6">
          <Link href="/dashboard">
            <Button variant="outline">← Volver al Dashboard</Button>
          </Link>
        </div>

        {/* Course Header */}
        <Card className="mb-8 p-6">
          <div className="grid md:grid-cols-2 gap-6">
            <div>
              <h1 className="text-3xl font-bold text-gray-900 mb-2">
                {course.name}
              </h1>
              <p className="text-gray-600 mb-4">{course.description}</p>
              <div className="space-y-2">
                <p className="text-gray-700">
                  <strong>Profesor:</strong> {course.teacher}
                </p>
                <p className="text-gray-700">
                  <strong>Horario:</strong> {course.schedule}
                </p>
              </div>
            </div>
            <div>
              <h3 className="text-xl font-semibold mb-3">Material del Curso</h3>
              <ul className="space-y-2">
                {course.materials.map((material, index) => (
                  <li
                    key={index}
                    className="flex items-center text-gray-700"
                  >
                    <span className="mr-2">📚</span>
                    {material}
                  </li>
                ))}
              </ul>
            </div>
          </div>
        </Card>

        {/* Video Conference Section */}
        <section className="mb-8">
          <h2 className="text-2xl font-semibold mb-4">Videoconferencia</h2>
          <VideoConference courseId={courseId} courseName={course.name} />
        </section>
      </div>
    </div>
  )
}
