"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"

interface VideoConferenceProps {
  courseId: string
  courseName: string
}

export default function VideoConference({ courseId, courseName }: VideoConferenceProps) {
  const [isActive, setIsActive] = useState(false)
  const [isRecording, setIsRecording] = useState(false)
  const [participants] = useState([
    { id: 1, name: "Juan García", role: "Alumno" },
    { id: 2, name: "María González", role: "Profesor" },
    { id: 3, name: "Carlos Rodríguez", role: "Alumno" },
  ])

  const toggleConference = () => {
    setIsActive(!isActive)
  }

  const toggleRecording = () => {
    setIsRecording(!isRecording)
  }

  return (
    <div className="grid grid-cols-1 lg:grid-cols-4 gap-4">
      {/* Main Video Area */}
      <div className="lg:col-span-3">
        <Card className="p-4">
          <div className={`aspect-video bg-gray-900 rounded-lg mb-4 flex items-center justify-center ${!isActive ? 'text-white' : ''}`}>
            {!isActive ? (
              <p className="text-center">
                Haga clic en "Iniciar Videoconferencia" para comenzar
              </p>
            ) : (
              <div className="w-full h-full bg-black relative">
                {/* Video would be embedded here in a real implementation */}
                <div className="absolute bottom-4 right-4 bg-gray-800 text-white px-3 py-1 rounded-lg text-sm">
                  {isRecording && "🔴 Grabando"}
                </div>
              </div>
            )}
          </div>

          {/* Controls */}
          <div className="flex flex-wrap gap-2">
            <Button
              onClick={toggleConference}
              variant={isActive ? "destructive" : "default"}
            >
              {isActive ? "Finalizar" : "Iniciar"} Videoconferencia
            </Button>
            {isActive && (
              <>
                <Button
                  variant="outline"
                  onClick={toggleRecording}
                >
                  {isRecording ? "Detener" : "Iniciar"} Grabación
                </Button>
                <Button variant="outline">
                  Compartir Pantalla
                </Button>
                <Button variant="outline">
                  Silenciar Micrófono
                </Button>
              </>
            )}
          </div>
        </Card>
      </div>

      {/* Participants Sidebar */}
      <Card className="p-4">
        <h3 className="font-semibold mb-4">Participantes</h3>
        <div className="space-y-2">
          {participants.map((participant) => (
            <div
              key={participant.id}
              className="p-2 bg-gray-50 rounded-lg flex items-center justify-between"
            >
              <div>
                <p className="font-medium">{participant.name}</p>
                <p className="text-sm text-gray-500">{participant.role}</p>
              </div>
              <div className="w-2 h-2 bg-green-500 rounded-full"></div>
            </div>
          ))}
        </div>
      </Card>
    </div>
  )
}
